// 模拟用户登录
const userMap = {
  testB: { // 一个B级用户
    token: '6d9b08f0-e50f-4cc0-90c3-4918183c3945'
  },
  testC: { // 一个C级用户
    token: '36591a69-8e72-4a1e-bb20-8de6151d41a1'
  }
}

export default {
  loginByUsername: config => {
    const {username} = JSON.parse(config.body)
    return userMap[username]
  }
}



