// 模拟获取用户基础信息

export default {
  // 获取用户级别
  queryUserLevel: ({body}) => {
    const {token} = JSON.parse(body)
    const level = token === '6d9b08f0-e50f-4cc0-90c3-4918183c3945' ? 'b' : 'c'
    return {level}
  },
  // 查询用户总额 累计销售额 当月销售额 累计收益 当月收益
  queryUserTotal: _ => {
    const sumSale = Math.round(Math.random() * 100000000) / 100
    const monthSale = Math.round(sumSale * 100 / 12) / 100
    const sumIncome = Math.round(sumSale * 100 / 20) / 100
    const monthIncome = Math.round(sumIncome * 100 / 12) / 100
    return {
      monthSale, // 月销售额
      sumSale, // 累计销售额
      monthIncome, // 月收益
      sumIncome// 累计收益
    }
  }
}



