// 获取用户的基础情况
import request from '@/utils/request'

// 查询用户级别
export function queryUserLevel (token) {
  const data = {
    token
  }
  return request({
    url: '/user/level',
    method: 'get',
    data
  })
}

// 查询用户总额 累计销售额 当月销售额 累计收益 当月收益
export function queryUserTotal (token) {
  const data = {
    token
  }
  return request({
    url: '/user/total',
    method: 'get',
    data
  })
}



