import Mock from 'mockjs'
import loginAPI from './login'
import basicAPI from './basic'
import orderAPI from './order'
Mock.setup({
  timeout: '200-500'
})
// 登录相关
Mock.mock(/\/login\/login/, 'post', loginAPI.loginByUsername)

// 用户基础相关
Mock.mock(/\/user\/level/, 'get', basicAPI.queryUserLevel) // 获取用户等级
Mock.mock(/\/user\/total/, 'get', basicAPI.queryUserTotal) // 获取用户的收益概况

// 订单查询相关
Mock.mock(/\/order\/personal/, 'get', orderAPI.queryPersonalOrders) // 个人订单查询
Mock.mock(/\/order\/team/, 'get', orderAPI.queryTeamOrders) // 团队订单查询
Mock.mock(/\/order\/training/, 'get', orderAPI.queryTrainingOrders) // 培训订单查询
Mock.mock(/\/order\/detail/, 'get', orderAPI.queryOrderDetail) // 订单详情
Mock.mock(/\/order\/record/, 'get', orderAPI.queryRecords) // 往来记录

export default Mock



