// 订单模拟数据
import orderList, { recordList } from './orderList'

export default {
  /** 获取用户个人订单
   * 传递 用户token / 开始时间字符串 / 结束时间字符串 / 分页查询量 / 分页位置 如：
   *  {
     *  token: '6d9b08f0-e50f-4cc0-90c3-4918183c3945',
     *  start: '2018-04-01 00:00:00.000+08:00', // 2018-4-1
     *  end: '2018-04-14 23:59:59.999+08:00', // 2018-4-14
     *  num: 20 ,// 查询20条
     *  page: 0 // 第0页
     *  }
   *  返回 该时间段内 销售额 / 收益 / 该时间段内订单总数 / 分页获取的订单详情 如 :
   *  {
     *    sale: 10000.00,  // 销售额
     *    income: 100.00   // 收益
     *    orderSum: 312,   // 该时间段内的订单总数
     *    orderList: [     // 订单详情
     *       {
     *         id: '7b5f5bdb-1269-4e50-9719-86754a8fec3f',
     *         sale: 200.00, // 销售额
     *         income: 10.00 // 收益
     *         userNick: '晴天', // 用户昵称
     *         userId: '7b5f5bdb-1269', //用户id
     *         userAvatar: 'https://placeimg.com/80/80/any?random=123' // 头像
     *         userStarLevel: 2, // 用户星级
     *         date: '2018-04-13 09:30:26.123+08:00' // 成交时间
     *       },
     *       {
     *         id: '7b5f5bdb-1269-4e50-9719-86754a8fec3f',
     *         sale: 200.00, // 销售额
     *         income: 10.00 // 收益
     *         userId: '7b5f5bdb-1269', //用户id
     *         userNick: '阴天', // 用户昵称
     *         userAvatar: 'https://placeimg.com/80/80/any?random=234' // 头像
     *         userStarLevel: 0, // 用户星级
     *         date: '2018-04-13 09:30:26.123+08:00' // 成交时间
     *       },
     *       ...
     *    ]
     *  }
   */
  queryPersonalOrders: ({body}) => {
    const {token, start, end, num, page} = JSON.parse(body)
    console.log(token, start, end, num, page)
    const localList = orderList.slice(0, 23)
    return {
      sale: 9998.99,
      income: 1253.87,
      orderSum: 23,
      orderList: localList.slice(page * num, (page + 1) * num)
    }
  },

  /** 获取用户团队订单
   * 传递 用户token / 开始时间字符串 / 结束时间字符串 / 分页查询量 / 分页位置 如：
   *  {
     *  token: '6d9b08f0-e50f-4cc0-90c3-4918183c3945',
     *  start: 1522547142000, // 2018-4-1
     *  end: 1523584081695, // 2018-4-13
     *  num: 20 ,// 查询20条
     *  page: 0 // 第0页
     *  }
   *  返回 该时间段内 销售额 / 收益 / 该时间段内订单总数 / 分页获取的订单详情 如 :
   *  {
     *    sale: 10000.00,  // 销售额
     *    income: 100.00   // 收益
     *    orderSum: 312,   // 该时间段内的订单总数
     *    orderList: [     // 订单详情
     *       {
     *         sale: 200.00, // 销售额
     *         income: 10.00 // 收益
     *         userNick: '晴天', // 用户昵称
     *         userId: '7b5f5bdb-1269', //用户id
     *         userStarLevel: 2, // 用户星级
     *         userAvatar: 'https://placeimg.com/80/80/any?random=123' // 头像
     *         date: '2018-04-13 09:30:26.123+08:00' // 成交时间
     *       },
     *       {
     *         sale: 200.00, // 销售额
     *         income: 10.00 // 收益
     *         userNick: '阴天', // 用户昵称
     *         userId: '7b5f5bdb-1269', //用户id
     *         userStarLevel: 2, // 用户星级
     *         userAvatar: 'https://placeimg.com/80/80/any?random=234' // 头像
     *         date: '2018-04-13 09:30:26.123+08:00' // 成交时间
     *       },
     *       ...
     *    ]
     *  }
   */
  queryTeamOrders: ({body}) => {
    const {token, start, end, num, page} = JSON.parse(body)
    console.log(token, start, end, num, page)
    return {
      sale: 8456.99,
      income: 647.76,
      orderSum: 43,
      orderList: orderList.slice(page * num, (page + 1) * num)
    }
  },

  /** 获取用户培训订单
   * 传递 用户token / 开始时间字符串 / 结束时间字符串 / 分页查询量 / 分页位置 如：
   *  {
     *  token: '6d9b08f0-e50f-4cc0-90c3-4918183c3945',
     *  start: 1522547142000, // 2018-4-1
     *  end: 1523584081695, // 2018-4-13
     *  num: 20 ,// 查询20条
     *  page: 0 // 第0页
     *  }
   *  返回 该时间段内 销售额 / 收益 / 该时间段内订单总数 / 分页获取的订单详情 如 :
   *  {
     *    sale: 10000.00,  // 销售额
     *    income: 100.00   // 收益
     *    orderSum: 312,   // 该时间段内的订单总数
     *    orderList: [     // 订单详情
     *       {
     *         sale: 200.00, // 销售额
     *         income: 10.00 // 收益
     *         userId: '7b5f5bdb-1269', //用户id
     *         userNick: '晴天', // 用户昵称
     *         userStarLevel: 2, // 用户星级
     *         userAvatar: 'https://placeimg.com/80/80/any?random=123' // 头像
     *         date: '2018-04-13 09:30:26.123+08:00' // 成交时间
     *       },
     *       {
     *         sale: 200.00, // 销售额
     *         income: 10.00 // 收益
     *         userId: '7b5f5bdb-1269', //用户id
     *         userNick: '阴天', // 用户昵称
     *         userStarLevel: 2, // 用户星级
     *         userAvatar: 'https://placeimg.com/80/80/any?random=234' // 头像
     *         date: '2018-04-13 09:30:26.123+08:00' // 成交时间
     *       },
     *       ...
     *    ]
     *  }
   */
  queryTrainingOrders: ({body}) => {
    const {token, start, end, num, page} = JSON.parse(body)
    console.log(token, start, end, num, page)
    const localList = orderList.slice(0, 3)
    return {
      sale: 1426.99,
      income: 98.00,
      orderSum: 3,
      orderList: localList.slice(page * num, (page + 1) * num)
    }
  },
  /*
 * 获取订单详情
 *  传递 用户token / 订单id 如：
 *  {
 *   token: '6d9b08f0-e50f-4cc0-90c3-4918183c3945',
 *   id: '7b5f5bdb-1269-4e50-9719-86754a8fec3f'
 *  }
 *  返回 该订单详情如下
 *  {
 *         id: '7b5f5bdb-1269-4e50-9719-86754a8fec3f',  // 交易id 流水号
 *         sale: 200.00, // 销售额
 *         income: 10.00, // 收益
 *         userNick: '阴天', // 用户昵称
 *         userId: '7b5f5bdb-1269', //用户id
 *         userAvatar: 'https://placeimg.com/80/80/any?random=234', // 头像
 *         userStarLevel: 2, // 用户星级
 *         date: '2018-04-13 09:30:26.123+08:00', // 成交时间
 *         payMethod: 'wechat' // 支付方式
 *         content: '大前门天然矿泉水喷雾爽肤水',  // 交易内容简介
 *         assort: '化妆品', // 交易分类
 *         transport: '运输中',  // 物流信息
 *       },
 * */
  queryOrderDetail: ({body}) => {
    const {token, id} = JSON.parse(body)
    console.log(token, id)
    let order = orderList.find(o => o.id === id)
    console.log(order)
    order = Object.assign({}, order, {
      payMethod: 'wechat',
      content: '大前门天然矿泉水喷雾爽肤水',
      assort: '化妆品',
      transport: '已发货'
    })
    return order
  },

  /**
   * 分页获取当前用户与某用户的往来记录
   * TODO 分页获取往来记录
   *  传递 当前用户token / 查询用户 userId  如：
   *  {
 *    token: '6d9b08f0-e50f-4cc0-90c3-4918183c3945',
 *    userId: '7b5f5bdb'
 *  }
   *  直接返回list数组 无需按月分离 前端将进行统计
   */
  queryRecords: ({body}) => {
    const {token, userId} = JSON.parse(body)
    console.log(token, userId)
    return {
      list: recordList
    }
  }

}



